.signin {
  box-sizing: border-box;
  max-width: 340px;
  width: 100%;
  border-radius: 10px;
  flex-direction: column;
  padding: 50px 30px 30px;
  font-family: inherit;
  box-shadow: 0 0 6px #0003;
}

.signin__title {
  color: #1e1e1e;
  letter-spacing: 0;
  text-align: center;
  margin: 0 0 50px;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
}

.signin__input {
  width: 100%;
  color: #1e1e1e;
  border: none;
  border-bottom: 1px solid #2e69fd;
  outline: none;
  padding-bottom: 7px;
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
}

.signin__label {
  color: #999;
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
  position: absolute;
  top: 6px;
  left: 0;
}

.signin__link {
  text-align: center;
  color: #2e69fd;
  font-size: 11px;
  font-weight: 500;
  line-height: 11px;
  text-decoration: none;
  display: block;
}

